import React from "react";
import { Modal, Button } from "react-bootstrap";
import "./ExperienceCard.css";
export default function ExperienceCardModal({ company, isModalVisible }) {

        return (
            <div className="experience-card-modal">
                <Modal show = {isModalVisible} aria-labelledby="contained-modal-title-vcenter" centered>
                    <Modal.Header closeButton>
                        <Modal.Title> {company.companyName} </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>{
                        company.jobRoles.map((role)=>{
                            return (
                            <p className="subTitle skills-text">{role}</p>
                            );
                        })}
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary">Close</Button>
                        <Button variant="primary">Save changes</Button>
                    </Modal.Footer>
                </Modal>
            </div>
        );
}
