import React from "react";
import "./Experience.css";
import ExperienceCard from "../../components/experienceCard/ExperienceCard";
import { experienceSection } from "../../portfolio";
import { Fade } from "react-reveal";

export default function experience() {
  return (
    <Fade bottom duration={1000} distance="20px">
    <div className="main" id="experiences">
      <div className="experience-main-div">
        <div className="experience-header">
          <h1 className="skills-heading">{experienceSection.title} </h1>
          <p className="subTitle experience-subtitle">{experienceSection.subTitle}</p>
        </div>
        <div className="experience-cards-div">
          {experienceSection.companies.map(company => {
            return (
              <ExperienceCard company={company} />
            );
          })}
        </div>
      </div>
    </div>
    </Fade>
  );
}
