import React, { useState } from "react";
import ExperienceCardModal from "./ExperienceCardModal";
import "./ExperienceCard.css";
export default function ExperienceCard({ company }) {
  const [isModalVisible, toggleModal] = useState(false);
  return (
      <div className="experience-card" onClick={()=> toggleModal(!isModalVisible)}>
        <img className="company-logo" src={company.companyLogo}></img>
          <p className="subTitle experience-information"> {company.description} </p>
          <p className = "subTitle experience-information"> {company.duration} </p>
        <ExperienceCardModal isModalVisible={isModalVisible} company={company} />
      </div>
  );
}
